.product {
  margin-bottom: 30px;
  padding: 20px;
  border: 1px solid map-get($grey-colors, 200);
  border-radius: 5px;

  .picture {
    float: left;
    width: 120px;
    margin-right: 20px;
  }

  &-content {
    overflow: hidden;

    .cate {
      @include font-size(12px);
      display: block;
      color: map-get($grey-colors, 700);
      text-transform: uppercase;
      margin-bottom: 10px;
    }
    .name {
      @include truncate-line(1);
      @include font-size(18px);
      font-weight: map-get($font-weight, bold);
      min-height: 19px;
      margin-bottom: 10px;
    }
  }

  &-feature {
    min-height: 150px;
  }

  .price {
    @include font-size(14px);
    color: map-get($grey-colors, 700);
    font-weight: 500;
  }

  .regular-price {
    @include font-size(14px);
    color: map-get($grey-colors, 700);
    font-weight: 400;
    text-decoration: line-through;
  }
  .new-price {
    color: map-get($colors, danger);
    margin-left: 10px;
  }
}

.hook-reviews {
  margin-bottom: 10px;
}
