$mediaQueryUnit: 1px;
$maxQuery: max;
$minQuery: min;

// mixin for import font from local file
@mixin font-face($name, $file, $weight: normal, $style: normal){
  @font-face {
    font-family: "#{$name}";
    src: url('../fonts/#{$file}.eot');
    src: url('../fonts/#{$file}.eot?#iefix') format('embedded-opentype'),
         url('../fonts/#{$file}.woff') format('woff'),
         url('../fonts/#{$file}.ttf') format('truetype'),
         url('../fonts/#{$file}.svg?#webfont') format('svg');
    font-weight: $weight;
    font-style: $style;
  }
}

@mixin font-size($size) {
  font-size: $size;
}

@mixin font-family($family) {
  font-family: $family;
}

@mixin row() {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

@mixin flex-basic($col) {
  flex: 0 100% / $col;
}

@mixin set-width-height($width, $height) {
  width: $width;
  height: $height;
}

@mixin set-border($direction, $width, $style, $color) {
  border-#{$direction}: $width $style $color;
}

@function convert-pixel-to-rem($size) {
  @return $size / $initial-font-size * 1rem;
}

@mixin pseudo($display: block, $pos: absolute, $content: '') {
  content: $content;
  display: $display;
  position: $pos;
}

@mixin border-radius($radius) {
	border-radius: $radius;
	-webkit-border-radius: $radius;
	-moz-border-radius: $radius;
}

@mixin box-shadow($top, $left, $blur, $color, $inset: false) {
  @if $inset {
    -webkit-box-shadow: inset $top $left $blur $color;
    -moz-box-shadow: inset $top $left $blur $color;
    box-shadow: inset $top $left $blur $color;
  } @else {
    -webkit-box-shadow: $top $left $blur $color;
    -moz-box-shadow: $top $left $blur $color;
    box-shadow: $top $left $blur $color;
  }
}

@mixin transition($property, $duration, $easing: linear) {
	transition: $property $duration $easing;
	-webkit-transition: $property $duration $easing;
	-moz-transition: $property $duration $easing;
}

@mixin transition-property($property...) {
  -webkit-transition-property: $property;
  transition-property: $property;
}

@mixin button($color: $bg-primary, $text-color: $white) {
  @include font-family($raleway);
  @include transition(all, 0.3s, ease-in);

  display: inline-block;
  text-align: center;
  text-decoration: none;
  outline: none;
  cursor: pointer;

  color: $text-color;
  font-weight: map-get($font-weight, bold);
  background: $color;

  &:hover {
    text-decoration: none;
    opacity: 0.7;
  }

  &:active {
    position: relative;
  }
}

@mixin user-select() {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; 
}

@mixin input-placeholder {
  &.placeholder { @content; }
  &:-moz-placeholder { @content; }
  &::-moz-placeholder { @content; }
  &:-ms-input-placeholder { @content; }
  &::-webkit-input-placeholder { @content; }
}

@mixin clear-fix() {
  &:before,
  &:after {
    content: " "; 
    display: table; 
  }
}

@mixin hover-state {
  &:hover,
  &:active,
  &:focus {
      @content;
  }
}

@mixin truncate($truncation-boundary) {
  max-width: $truncation-boundary;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@mixin truncate-line($line) {
  display: -webkit-box;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

// DESKTOP FIRST
@mixin respond-below($breakpoint) {
  @if map-has-key($breakpoints, $breakpoint) {
    $width: map-get($breakpoints, $breakpoint);

    @media only screen and (max-width: $width) {
      @content;
    } 
  } @else {
    @warn "Invalid breakpoints: " #{$breakpoint};
  }
}

// MOBILE FIRST
@mixin respond-above($breakpoint) {
  @if map-has-key($breakpoints, $breakpoint) {
    $width: map-get($breakpoints, $breakpoint);

    @media only screen and (min-width: $width) {
      @content;
    } 
  } @else {
    @warn "Invalid breakpoints: " #{$breakpoint};
  }
}

// SPECIFY BREAKPOINT
@mixin respond-between($lower, $upper) {
  @if map-has-key($breakpoints, $lower) and map-has-key($breakpoints, $upper) {
    $width-lower: map-get($breakpoints, $lower);
    $width-upper: map-get($breakpoints, $upper);

    @media only screen and (min-width: $width-lower) and (max-width: $width-upper) {
      @content;
    } 
  } @else {
    @warn "Invalid breakpoints: " #{$breakpoint};
  }
}

@mixin media-queries($media, $type: max) {
  @if map-has-key($breakpoints, $media) {
    $width: map-get($breakpoints, $media);
    // @if $type == $minQuery {
    //   $width: $width + $mediaQueryUnit;
    // }
    @media only screen and (#{$type}-width: $width) {
      @content;
    }
  } @else {
    @media only screen and (min-width: 768px) {
      @content;
    }
  }
}
