/****************************************************
* GRID TEMPLATE SETTING 
****************************************************/
$grid-cols: 12;

.container {
  width: 100%;
  margin: 0 auto;
  
  .row {
    @include row();
    margin: 0 -15px;
  }
  .row-reverse {
    flex-direction: row-reverse;
  }
  .col {
    -ms-flex-preferred-size: 0;
    flex-basis: 0;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    max-width: 100%;
    padding: 0 15px;
  }
  .box {
    padding: 24px;
  }

  @for $i from 1 through $grid-cols {
    .col-#{$i} {
      -webkit-box-flex: 0;
      -ms-flex: 0 (100 / ($grid-cols / $i)) * 1%;
      flex: 0 (100 / ($grid-cols / $i)) * 1%;
      max-width: (100 / ($grid-cols / $i)) * 1%;
    }
  }
}

@each $key, $value in $breakpoints {
  @include media-queries($key, min) {
    @for $i from 1 through $grid-cols {
      .col-#{$key}-#{$i} {
        -webkit-box-flex: 0;
        -ms-flex: 0 (100 / ($grid-cols / $i)) * 1%;
        flex: 0 (100 / ($grid-cols / $i)) * 1%;
        max-width: (100 / ($grid-cols / $i)) * 1%;
      }
    }
  }
}
