.menu {
  .menu-item {
    @include font-size(1.6rem);
    
    > a {
      display: inline-block;
      font-weight: map-get($font-weight, bolder);
      color: $text-default;
      border: 1px solid transparent;

      &:hover {
        border-bottom: 1px solid map-get($colors, secondary);
      }

      span {
        line-height: 1.5;
      }
    }
    &:not(:first-child) {
      margin-left: 36px;
    }
  }
  .dropdown-menu {
    vertical-align: middle;
    margin-left: 5px;
  }

  .has-child {
    position: relative;
  }

  .menu-dropdown {
    opacity: 0;
    visibility: hidden;
    transform-origin: 0% 0%;
    transform-style: preserve-3d;
    transform: rotateX(-75deg);
    position: absolute;
    min-width: 250px;
    padding-top: 15px;

    .sub-menu {
      background-color: $white;
      transition: all 0.3s;

      a {
        padding: 12px;
        border-bottom: 1px solid map-get($grey-colors, 200);

        &:hover {
          padding-left: 20px;
          color: map-get($colors, primary);
        }
      }
    }
  }
  .has-child:hover > .menu-dropdown {
    opacity: 1;
    visibility: visible;
    transform: rotateX(0deg);
    transition: transform 0.3s,opacity 0.3s
  }
}
