.cart {
  margin-left: 30px;
  &-icon {
    @include set-width-height(40px, 40px);
    display: inline-block;
    vertical-align: middle;
  }

  &-number {
    @include set-width-height(24px, 24px);
    display: inline-block;
    vertical-align: middle;
    border-radius: 50%;
    background: map-get($colors, primary);
    text-align: center;
    line-height: 1.5;
    color: $white;
    margin-right: 10px;
  }
}
