/****************************************************
* GENERAL SETTING 
****************************************************/
html {
  @include font-size(10px);
  font-family: $primary-font;
  margin: 0;
  padding: 0;
}
body {
  @include font-size(16px);
  position: relative;
  margin: 0;
  padding: 0;
  color: $text-default;
}
a {
  @include transition(all, 0.3s, ease-in);
  display: block;
  text-decoration: none;
  color: $text-default;
}
a:hover {
  opacity: 0.7;
  cursor: pointer;
}
  