.header {
  position: relative;
  z-index: map-get($z-index, tooltip);

  &-top {
    position: absolute;
    left: 0;
    right: 0;
    padding: 35px 15px;
  }

  &-scroll {
    position: fixed;
    top: 0;
    background: $white;
    padding: 15px;
  }

  #logo h1 {
    padding-left: 35px;
  }

  &-search-cart {
    display: flex;
    align-items: center;
  }
}
